import React, { useState, useEffect } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import 'react-pdf/dist/esm/Page/TextLayer.css';
// import './PDFViewer.css'; // Optional, for custom styling

interface PDFViewerProps {
  pdfUrl: string;
}

function PDFViewer({ pdfUrl }: PDFViewerProps) {
  const [numPages, setNumPages] = useState<number | null>(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [windowWidth, setWindowWidth] = useState(0);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    // Configure PDF.js worker
    pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;
    
    // Set initial window width
    setWindowWidth(window.innerWidth);
    
    const handleResize = () => setWindowWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  function onDocumentLoadSuccess({ numPages }: { numPages: number }) {
    setNumPages(numPages);
    setPageNumber(1);
    setError(null);
  }

  function onDocumentLoadError(error: Error) {
    console.error('Error loading PDF:', error.message);
    setError('Failed to load PDF. Please try again later.');
  }

  const scaleFactor = windowWidth > 768 ? 1.5 : 0.75;

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-4">
      <div className="bg-white p-4 rounded-lg shadow-lg max-w-4xl w-full">
        {error ? (
          <div className="text-center text-red-500 p-4">
            {error}
          </div>
        ) : (
          <Document
            file={pdfUrl}
            onLoadSuccess={onDocumentLoadSuccess}
            onLoadError={onDocumentLoadError}
            loading={
              <div className="flex justify-center items-center p-8">
                <p className="text-gray-600">Loading PDF...</p>
              </div>
            }
            noData={
              <div className="text-center text-gray-600 p-4">
                No PDF file found. Please check if the file exists in the public directory.
              </div>
            }
            options={{
              cMapUrl: 'https://unpkg.com/pdfjs-dist@3.4.120/cmaps/',
              cMapPacked: true,
            }}
          >
            <Page 
              pageNumber={pageNumber} 
              scale={scaleFactor}
              className="max-w-full"
              loading={
                <div className="flex justify-center items-center p-4">
                  <p className="text-gray-600">Loading page...</p>
                </div>
              }
            />
          </Document>
        )}

        {numPages && !error && (
          <div className="flex justify-center items-center gap-4 mt-4">
            <button
              type="button"
              disabled={pageNumber <= 1}
              onClick={() => setPageNumber(prev => prev - 1)}
              className="px-4 py-2 bg-blue-500 text-white rounded disabled:bg-gray-300"
            >
              Previous
            </button>
            <p className="text-gray-700">
              Page {pageNumber} of {numPages}
            </p>
            <button
              type="button"
              disabled={numPages !== null && pageNumber >= numPages}
              onClick={() => setPageNumber(prev => prev + 1)}
              className="px-4 py-2 bg-blue-500 text-white rounded disabled:bg-gray-300"
            >
              Next
            </button>
          </div>
        )}
      </div>
    </div>
  );
}

export default PDFViewer;
